.likes__count {
  position: relative;
  display: block;
  margin-bottom: 39px;
  padding-left: 37px;

  font-size: 14px;
  line-height: 24px;
  font-weight: 700;
  text-transform: uppercase;

  color: $black;

  &--vegetation {
    margin-bottom: 40px;

    @media (min-width: $tablet-width) {
      margin-left: 36px;
    }
  }

  &::before {
    position: absolute;
    width: 23px;
    height: 21px;

    content: "";
    left: 0;
    top: -2px;
    cursor: pointer;

    background: url("../img/svg/icon-like.svg") no-repeat 0 0;
    opacity: 0.3;
  }
}

.likes__count:hover::before {
  opacity: 0.6;

  @media (min-width: $desktop-width) {
    opacity: 0.8;
  }
}

.likes__count:active::before {
  opacity: 0.8;

  @media (min-width: $desktop-width) {
    opacity: 0.6;
  }
}


@media (min-width: $tablet-width) {
  .likes__count {
    margin-bottom: 0;

    &--spaces {
      margin-left: 395px;
    }

    &--road {
      margin-left: 103px;
    }

    &--bridge {
      margin-left: 102px;
    }

    &::before {
      left: 6px;
    }
  }
}

@media (min-width: $desktop-width) {
  .likes__count {
    color: $white;

    &--spaces {
      margin-left: 945px;
    }

    &--vegetation {
      margin-left: 112px;
    }

    &--road {
      margin-left: 177px;
    }

    &--bridge {
      margin-left: 178px;
    }


    &::before {
      opacity: 1;
